import { memo, useEffect } from "react";

const experience = [
    { id: 0, number: 3, title: "Projects Made", image: "coding.svg"},
    { id: 1, number: 2, title: "Years Experience", image: "star.svg" },
    { id: 2, number: 6, title: "Web Technologies", image: "world.svg"  }
];

function Experience() {

    function scroller() {
        const cards = [...document.getElementsByClassName("exp-card")];
        const nums = [...document.getElementsByClassName("exp-num")];
        const info = [...document.getElementsByClassName("exp-info")];
        cards.forEach((ele, index) => {
            if(ele.getBoundingClientRect().top < 500) {
                nums[index].style.transistion = "transform 0.5s";
                nums[index].style.color = "var(--peach-dark)";
                nums[index].style.animation = "0.5s zoomIn ease-out forwards";
                nums[index].classList.remove("opacity-0");
                
                info[index].style.animation = "0.5s fadeInUp ease-out forwards";
                info[index].classList.remove("opacity-0");
            }
        });
    }

    useEffect(() => {
        window.addEventListener("scroll", scroller);
        return () => window.removeEventListener("scroll", scroller);
    }, []);

    return (
        <>
            <div className="container exp row g-0 mx-auto">
                {
                    experience.map(element => {
                        return (
                            <div id={"exp-" + element.id} key={element.id} className="card exp-card col-12 col-sm-6 col-lg-4 px-lg-3 px-2 my-2 mx-auto">
                                <div className="card-body exp-card-body shadow p-2 p-lg-5 position-relative">
                                    <div className="exp-num display-inline opacity-0 d-block text-center">{element.number}</div>
                                    <p className="opacity-0 text-secondary exp-info text-center fs-4">{element.title}</p>
                                </div>
                            </div>
                        );
                    })
                }
            </div>
        </>
    );
}

export default memo(Experience);