import { memo, useState, useEffect } from "react";

function Typer(props) {
    let [info, setInfo] = useState([]);
    
    function setInfoByLetter(msg, new_msg) {
        let ind = 0;
        let temp = {...new_msg};
        temp.statement = "";
        temp.response = "";
        setInterval(() => {
            if(ind >= new_msg.statement.length)
                return;
            if(ind == new_msg.statement.length) {
                temp.response = new_msg.response;
            }
            temp.statement += new_msg.statement[ind++];
            info = [...msg, temp];
            setInfo(info);
        }, 25);
    }

    function type() {
        let content = props.info;
        let index_o = 0, index_i = 0;
        let toggle = false;

        setInterval(() => {
            if(index_o >= content.length)
                return;

            if(!toggle) {
                setInfoByLetter(info, content[index_o]);
                toggle = true;
            } else {
                let temp = content[index_o];
                info[info.length - 1].response = temp.response;
                toggle = false;
                index_o ++;
            }
            setInfo(info);
        }, 500);
    }

    useEffect(() => {
        let mounted = true;
        if(mounted)
            type();
        return () => {
            mounted = false;
        }
    }, []);

    return (
        <>
            <div id="git-typer" className="mx-auto typer col-12 col-md-8 col-lg-6">
                <div className="typer-header d-flex">
                    <div className="dot-holder">
                        <div className="dot-red"></div>
                        <div className="dot-yellow"></div>
                        <div className="dot-green"></div>
                    </div>
                    <button className="btn btn-close float-end my-2 mx-3" />
                </div>
                <div className="typer-body bg-dark" style={{fontFamily: "IBM Plex Mono"}}>
                    {
                        info.map(ele => {
                            return (
                                <div id={"git-text-" + ele.id} key={ele.id}>
                                <p className="text-info">shrey-naik(master):~$ <span className="text-light">{ele.statement}</span></p>
                                <p className="text-success">{ele.response}</p>
                                </div>
                            );
                        })
                    }
                </div>
            </div>
        </>
    );
}

export default memo(Typer);