import { memo, useEffect } from "react";

function headerLoad(ele) {
    const header1 = document.getElementById("intro-header-1");
    const header2 = document.getElementById("intro-header-2");
    setTimeout(() => {
        header1.classList.remove("d-none");
        header1.style.animation = "0.3s fadeIn ease-out forwards";
    }, 1100);
    setTimeout(() => {
        header2.classList.remove("d-none");
        header1.style.transistion = "transform 1.3s";    
        header2.style.animation = "0.3s fadeInBottom ease-out forwards";  
        
        const scroll = document.getElementById("scroll-gif");
        scroll.style.animation = "0.5s fadeInUp ease-out forwards"
        scroll.classList.remove("opacity-0");
    }, 2000);
}

function IntroDiv() {

    useEffect(() => {
        let mounted = true;
        const init = () => headerLoad(document.getElementById("intro-header"));
        
        if(mounted)
            init();

        return () => {
            mounted = false;
        }
    }, []);

    return(
        <>
            <div className="position-relative intro-div-pic min-vh-100">
                <div className="container-fluid my-basic-info d-block">
                    <h1 id="intro-header" className="text-light text-center px-lg-5 mx-lg-5 fw-bolder"><span id="intro-header-1" className="d-none">Hi.</span><span id="intro-header-2" className="d-none"> I'm Shrey.</span></h1>
                </div>
                <img id="scroll-gif" className="scroll-gif opacity-0" src={process.env.PUBLIC_URL + "./images/misc/scroll.gif"} />
            </div>
        </>
    );
}

export default memo(IntroDiv);