import { memo, useEffect, useState } from "react";
import Dropzone from "react-dropzone";
import "../styles/projects.css";
import makeActive from "./link-changer";
import skills from "./skill-info";

const projects = [
    { id: 0, name: "Deep Colors", languages: "PHP, HTML, CSS, MySQL", desc: "Deep Colors is an online shopping emporium made by me and my colleague. It is a completely working website which include basic CRUD operations and core PHP and HTML and CSS to statically make the webpages. It is fully functional with functionalities such as a cart for each user, billing system, etc. We have also provided front and back end validation to reduce the users which don't fit in to the constraints. It also has a URL validation in PHP so that the billing system can't be accessed without a cart, a cart can't be present without an account and so on.", src: process.env.PUBLIC_URL + "./images/projects/Deep-Colors.png", link: "https://www.github.com/naikshrey2308/ICTWIIb_Project_CE062_CE064", images: [ "dc-1.JPG", "dc-2.JPG" ] },

    { id: 1, name: "E-Scritoire", languages: "JavaScript, Bootstrap 5, jQuery, HTML, CSS", desc: "E-Scritoire is a website to increase your productivity by making the use of some basic utilities that we provide like a text editor, a clock and stopwatch which runs anywhere on the website in background even if you leave a particular webpage. We also have a calculator handy when you need it. The text editor allows you to customize your document in a way such that you could give it your own touch.", src: process.env.PUBLIC_URL + "./images/projects/E-Scritoire.png", link: "https://github.com/naikshrey2308/WDW_Project_CE062_CE064_CE070", images: [], live: "https://naikshrey2308.github.io/WDW_Project_CE062_CE064_CE070" },

    { id: 2, name: "E-Notes Browser Extension", languages: "JavaScript, Bootstrap 5, jQuery", desc: "E-Notes is a browser extension that can be installed locally on your computer to keep a track of the important points on the websites you might want to note down. This completely reduces the boring old procedure of opening a text editor, saving the file frequently, etc. In this extension, the notes are automatically saved once you create them. It also has the feature to organise your notes in different notebooks by topics. Also, it provides great text customization and note customization to make the notebook feel like yours!", src: process.env.PUBLIC_URL + "./images/projects/E-Notes.png", link: "https://github.com/naikshrey2308/E-Notes", images: [ "en-1.JPG", "en-2.JPG", "en-3.JPG", "en-4.JPG", "en-5.JPG" ] },

    { id: 3, name: "Quizzio", languages: "JavaScript, Bootstrap 5, Django, Python, HTML, CSS, PostgreSQL", desc: "Quizzio is an online quiz application made completely in Django as back-end and with other basic front-end technologies. It provides you an innovative way to give and organize quizzes. The students' scores are recorded and appropriate data is processed to give performance charts to the users for improving their performance. It is meant for both students and organizers. It's a great web-app for quizzes!", src: process.env.PUBLIC_URL + "./images/projects/Quizzio.png", images: [ "q-1.JPG", "q-2.JPG", "q-3.JPG", "q-4.JPG", "q-5.JPG" ] },

    { id: 4, name: "Enormous Entrepot", languages: "JavaScript, Bootstrap 5, Java, Spring, Hibernate, PostgreSQL, HTML, CSS", desc: "This is an online food store for perishables. It is made using Spring Boot, Hibernate, Core Java, and some front-end technologies like Javascript, Html and Css. It provides all options from add-to-cart to payment, from creating an account to deleting. It has been built by keeping all perspectives in mind. Therefore, it has an admin panel too. Admin can regularly update the supplies and filter and work on the data of the website. It's a really nice website for shopping perishables online!", src: process.env.PUBLIC_URL + "./images/projects/Enormous Entrepot.png", images: [ "ee-1.JPG", "ee-2.JPG", "ee-3.JPG", "ee-4.JPG" ] },
];

function ProjectInfo() {
    return (
        <>
            <div className="container mt-5 pt-5">
                <h1 className="text-center" style={{fontSize: "50px"}}>PROJECTS</h1>
                <p id="project-info" className="my-5 container py-4">
                    As I'm a person who loves to develop and serve the community, I had come across many technologies in my learning path. As a consequence, of course I built some projects to get a strong hold on the newly learnt tech as well as to show my level of creativity and skill. I've learnt and worked on various front-end and back-end technologies a few of which are Oracle/Postgre/MySQL database systems, Java, Python and some frameworks for back end and JavaScript and other related technologies. Finally, I hereby bring you to the collection of my projects which I find the "best few" out of my collection.
                </p>
            </div>
        </>
    );
}

function ProjectActivator(props) {

    function ProjectLogos() {

        function makeActive(id) {
            const logos = [...document.getElementsByClassName("project-logo-img")];
    
            logos.forEach(ele => {
                if(ele.id.includes(id)) {
                    const holder = document.getElementById("project-dropper-icon");
                    holder.src = ele.src;
                    holder.style.animation = "0.5s zoomIn ease-out forwards";
                    document.getElementById("project-dropper-info").classList.add("d-none");

                    setCurrent(Number.parseInt(ele.id.replace("project-logo-", "")));
                    setSlide(0);
                    activateProject();
                }
            });
        }
    
        return (
            <>
                <div className="project-logos d-flex mx-auto">
                    {
                        projects.map(ele => {
                            return(
                                <>
                                    <img id={"project-logo-" + ele.id} key={"project-logo-img-" + ele.id} src={ele.src} width={100} className="project-logo-img m-3 p-2" style={{aspectRatio: "1 / 1"}} onClick={() => makeActive(ele.id)} />
                                </>
                            );
                        })
                    }
                </div>
                <div className="line" />
            </>
        );
    }



    let [current, setCurrent] = useState(0);
    let [slide, setSlide] = useState(0);
    let [fullscreen, setFullscreen] = useState(false);

    function toggleFullscreen(e) {
        if(!fullscreen) {
            document.getElementById("current-project").requestFullscreen();
            setFullscreen(true);
        } else {
            document.exitFullscreen();
            setFullscreen(false);
        }
    }

    function moveLeft() {
        let min = 0;
        if(slide > min) {
            setSlide(slide - 1);
        }
    }

    function moveRight() {
        let max = props.data[current].images.length + 2;
        if(props.data[current].live)
            max ++;
        if(slide < max) {
            setSlide(slide + 1);
        }
    }

    function getCurrentProject(file) {
        projects.forEach(ele => {
            if(ele.src.includes(file.name.toString().trim())) {
                setCurrent(ele.id);
                setSlide(0);
                return ele.id;
            }
        });
        return 0; 
    }

    const changeImage = (files) => {
        const ele = document.getElementById("project-dropper-icon");
        ele.src = URL.createObjectURL(files[0]);
        document.getElementById("project-dropper-info").classList.add("d-none");

        getCurrentProject(files[0]);
        activateProject();
    }
    
    function activateProject() {
        const dropper = document.getElementById("project-dropper-icon");
        const projector = document.getElementById("current-project");
        dropper.scrollIntoView({behavior: "smooth"});
    }

    useEffect(() => {}, [current, slide]);

    return (
        <>
            <ProjectLogos/>

            <div className="project-dropper mx-auto p-3 col-3 col-sm-2 col-md-1 shadow">
            <Dropzone multiple={false} accept={{ "image/*": [".png", ".jpg", ".jpeg", ".svg"] }} onDrop={acceptedFiles => changeImage(acceptedFiles)}>
                {
                    ({getRootProps, getInputProps}) => (
                            <div {...getRootProps()}>
                                <input {...getInputProps()} />
                                <p id="project-dropper-info">Drop a project</p>
                                <img id="project-dropper-icon" className="w-100" />
                            </div>
                    )
                }
            </Dropzone>
            </div>
            <div className="line" />

            <div id="current-project" className="min-vh-100 container bg-dark">
                <button className="btn position-absolute top-0 end-0 p-4" onClick={toggleFullscreen}><img src="./images/svgs/fullscreen.svg" /></button>
                <button className="btn position-absolute bottom-0 end-0 p-4" onClick={moveRight}><img src="./images/svgs/right.svg" /></button>
                <button className="btn position-absolute bottom-0 start-0 p-4" onClick={moveLeft}><img src="./images/svgs/left.svg" /></button>
                <a className="position-absolute top-0 start-0 p-4" target="_blank" href={props.data[current].link}><img src="./images/svgs/link.svg" /></a>

                {
                    slide == 0 && (
                    <div className="current-slide text-light">
                        <img src={props.data[current].src} className="current-project-img w-100 p-3" />
                        <h3 className="text-center">{props.data[current].name}</h3>
                    </div>)
                }

                {
                    slide == 1 && (
                    <div className="current-slide container text-light">
                        <h1 className="text-center text-secondary py-3">About This Project</h1>
                        <p className="my-5 fs-5 px-3" style={{textAlign: "justify", textAlignLast: "center"}}>{props.data[current].desc}</p>
                        <div></div>
                    </div>)
                }

                {
                    slide == 2 && (
                    <div className="current-slide container text-light">
                        <h1 className="text-center text-secondary py-3">Technologies Used</h1>
                        <p className="my-5 fs-5 px-3" style={{textAlign: "justify", textAlignLast: "center"}}>
                            {props.data[current].languages.replaceAll(",", " ")}
                        </p>
                        <div className="current-project-tech">
                            {
                                props.data[current].languages.split(",").map(ele => {
                                    return (
                                        <img src={"./images/skills/" + ele.trim() + ".png"} className="m-3" width={30} height={30} />
                                    );
                                })
                            }
                        </div>
                    </div>)
                }

                {
                    props.data[current].images.map((ele, index) => {
                        return ( slide == (3 + index) &&
                            <div className="current-slide container text-light">
                                <img src={`./images/projects/${ele}`} className="w-100 p-lg-3" />
                            </div>
                        );
                    })
                }

                {
                    slide == (3 + props.data[current].images.length) && (props.data[current].live) && (
                        <div className="current-slide container text-light">
                            <p className="text-center fs-4">Visit the website <a href={props.data[current].live} target="_blank">Live</a></p>
                        </div>
                    )
                }
                
            </div>
        </>
    );
}

function Projects() {

    return (
        <>
        <div id="Projects" className="bg-light py-3">
            <ProjectInfo/>
            <ProjectActivator data={projects} skills={skills} />
        </div>
        </>
    );
}

export default memo(Projects);