import { useEffect, useState } from "react";
import "../styles/skills.css";
import skills from "./skill-info";
import makeActive from "./link-changer";
import Typer from "./Typer";

const skillsAnimInformation = [
    {id: 1, statement: "git checkout -b misc", response: "Switched to new branch misc"},
    {id: 2, statement: "git checkout -b front_end", response: "Switched to new branch front_end"},
    {id: 3, statement: "git checkout -b back_end", response: "Switched to new branch back_end"},
    {id: 4, statement: "", response: ""}
];

function SkillsTable() {

    function joinBranches() {
        const branchHolder = document.getElementsByClassName("git-branches")[0];
        
        if(branchHolder.getBoundingClientRect().top > window.scrollY - 200)
            return;

        const branches = [...document.querySelectorAll("[class^='branch-start-']"), ...document.querySelectorAll("[class^='branch-end-']"), ...document.getElementsByClassName("extra")];
        
        branches.forEach((ele, index) => {
            ele.style.animation = "0.5s coverHeight ease-out forwards";
            ele.classList.remove("opacity-0");
        });

        window.removeEventListener("scroll", joinBranches);
    }

    useEffect(() => {
        window.addEventListener("scroll", joinBranches);
        return () => window.removeEventListener("scroll", joinBranches);
    });

    return (
        <>
            <div className="branch-start" style={{zIndex: "-1"}} />
            <img src="./images/skills/git.png" width="80px" className="mx-auto d-block my-5" />
            <div className="git-branches d-flex position-relative">

                <div className="container branch">
                    <div className="branch-start opacity-0" />
                    <div className="branch-start opacity-0" />
                    <div className="branch-start-left-start opacity-0" />
                    <div className="branch-start-left-end opacity-0" />
                    <div className="tooltip tooltip-bottom" style={{left: "16.67%"}}>Miscellaneous</div>
                {
                    skills.map(ele => {
                        return (ele.type == "misc") && (
                            <div key={"left-branch-" + ele.id}>
                                <div className="branch-start"></div>
                                <img src={ process.env.PUBLIC_URL + "./images/skills/" + ele.name + ".png" } width="50px" className="left-image image p-2" title={ele.description} />
                            </div>
                        );
                    })
                }
                </div>

                <div className="container branch">
                    <div className="branch-start extra opacity-0" />
                    <div className="tooltip tooltip-top" style={{left: "50%"}}>Front End</div>
                {
                    skills.map(ele => {
                        return (ele.type == "front-end") && (
                            <div key={"middle-branch-" + ele.id}>
                                <div className="branch-start"></div>
                                <img src={ process.env.PUBLIC_URL + "./images/skills/" + ele.name + ".png" } width="50px" className="image p-2" title={ele.description} />
                            </div>
                        );
                    })
                }
                </div>

                <div className="container branch">
                    <div className="branch-start opacity-0" />
                    <div className="branch-start opacity-0" />
                    <div className="branch-end-left-start opacity-0" />
                    <div className="branch-end-left-end opacity-0" />
                    <div className="tooltip tooltip-bottom" style={{left: "83.33%"}}>Back End</div>
                {
                    skills.map(ele => {
                        return (ele.type == "back-end") && (
                            <div key={"right-branch-" + ele.id}>
                                <div className="branch-start"></div>
                                <img src={ process.env.PUBLIC_URL + "./images/skills/" + ele.name + ".png" } width="50px" className="right-image image p-2" title={ele.description} />
                            </div>
                        );
                    })
                }
                </div>
            </div>
            <div className="my-5 py-5">&nbsp;</div>
        </>
    );
}

function SkillInfo() {
    return (
        <>
            <div id="skills-info" className="container mb-5 px-3 py-4 text-center">
                I am a developer and I have a plenty of skills and most of them, focused on Web Development. I like to learn and build front-end design through my creativity as well as to use fantastic back-end and database technologies which diverts me, of course, to the way of Full-Stack Development. So here is a quick demo of what I can build using React. The tree shows my skills divided into different categories and of course the root of the braches is Git.
            </div>
        </>
    );
}

function Skills() {

    function opener() {
        const skills = document.getElementById("Skills");
        skills.style.animation = "0.3s fadeInLeft ease-out forwards";
        skills.style.left = "0%";
    }

    function scrollerFunc(event) {
        event.stopPropagation();
    }

    function close() {
        const skills = document.getElementById("Skills");
        skills.style.animation = "0.3s fadeOutLeft ease-in forwards";
    }

    useEffect(() => {
        let mounted = true;
        const scroller = (event) => scrollerFunc(event);
        const skills = document.getElementById("Skills");

        if(mounted) {
            makeActive("skills-nav");
            opener();

            skills.addEventListener("scroll", scroller);
        }
        
        return () => {
            mounted = false;
            skills.removeEventListener("scroll", scroller);
        }
    }, []);

    return (
        <div id="Skills" className="position-absolute w-100">
            <h3 className="text-center fw-normal mt-5 py-5" style={{fontSize: "50px"}}>SKILLS</h3>
            <SkillInfo />
            <Typer info={skillsAnimInformation} />
            <SkillsTable />
        </div>
    );
}

export default Skills;