import "../styles/navbar.css";
import { useEffect, useState } from "react";
import { Outlet, Link } from "react-router-dom";

function Navbar() {
    let updated = false;

    function scrollHandler(event) {
        const navbar = document.getElementById("navbar");
        const link = document.querySelector("a.nav-link.active");
        if(!updated && window.scrollY > 5) {
            navbar.classList.remove("bg-transparent");
            navbar.classList.add("bg-light", "shadow");
            updated = true;
        }
        if(updated && window.scrollY <= 5) {
            navbar.classList.add("bg-transparent");
            navbar.classList.remove("bg-light", "shadow");
            updated = false;
        }
    }

    function makeActive(id) {
        const link = document.getElementById(id);
        const links = [...document.getElementsByClassName("nav-link")];
        links.forEach(ele => {
            ele.classList.remove("active");
        });
        link.classList.add("active");
    }

    function showContact() {
        const contact = document.getElementById("Contact");
        contact.style.animation = "0.3s slideInRight ease-out forwards";
        contact.style.right = "0%";
    }

    useEffect(() => {
        const scroller = (event) => { scrollHandler(event); };
        window.addEventListener("scroll", scroller, {passive: false});
        return () => { window.removeEventListener("scroll", scroller); };
    }, []);

    return (
        <>
        <nav id="navbar" className="navbar g-0 container-fluid navbar-expand-sm navbar-light bg-transparent fixed-top">
            <div className="container-fluid">
                {/* <a className="navbar-brand" href="#">Shrey Naik</a> */}
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarContent" aria-controls="navbarContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarContent">
                    <ul className="mx-auto link-holder navbar-nav me-auto mb-2 mb-lg-0">
                        <li className="nav-item">
                            <Link id="about-nav" className="nav-link active" aria-current="page" to="/" onClick={() => makeActive("about-nav")}>About</Link>
                        </li>
                        <li className="nav-item">
                            <Link id="skills-nav" className="nav-link" to="/skills" onClick={() => makeActive("skills-nav")}>Skills</Link>
                        </li>
                        <li className="nav-item">
                            <Link id="projects-nav" className="nav-link" to="/projects" onClick={() => makeActive("projects-nav")}>Projects</Link>
                        </li>
                        <li className="nav-item">
                            <Link id="gallery-nav" className="nav-link" to="/gallery">Gallery</Link>
                        </li>
                        <li className="nav-item">
                            <a className="nav-link" href="#Contact" onClick={showContact}>Contact</a>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>

        <Outlet />
        </>
    );
}

export default Navbar;