import { useState, memo, useEffect } from "react";

const education = [
    { id: 0, heading: "Dharmsinh Desai University", time: "2020 - Present", src: process.env.PUBLIC_URL + "./images/misc/DDU.jpg", content: "Pursuing B.Tech in Computer Science at Dharmsinh Desai University has been a journey of great experiences! This is the place where I started off with my dream of becoming a software engineer." },

    { id: 1, heading: "Divine Child High School", time: "2018 - 2020", src: process.env.PUBLIC_URL + "./images/misc/Divine_Child.jpg", content: "Some of my crucial years for getting into a well-known college were spent here. I completed my Higher Secondary Education in Science stream here." },

    { id: 2, heading: "St. Xavier's High School", time: "2006 - 2018", src: process.env.PUBLIC_URL + "./images/misc/Saint_Xaviers.jpeg", content: "A place where great memories of mine hold! The place which has a major impact on who I am today and from where I completed my Pre-Primary, Primary and Secondary education is this school." }
];

function Education() {
    const [curEduPtr, curEduPtrUpdate] = useState(0);

    function setActive(e) {
        e.target.parentElement.childNodes.forEach(element => {
            element.classList.remove("active");
        })
        e.target.classList.add("active");
        curEduPtrUpdate(Array.prototype.indexOf.call(e.target.parentElement.childNodes, e.target));
    }

    function scroller() {
        const eduBox = document.getElementById("education-box");
        const img = document.getElementById("edu-img");
        const info = document.getElementById("edu-info");
        if(img.getBoundingClientRect().top < window.scrollY - 150) {
            img.style.animation = "0.5s fadeInUp ease-in-out forwards";
            img.classList.remove("opacity-0");
            info.style.animation = "0.5s zoomIn ease-out forwards";
            info.classList.remove("opacity-0");

            window.removeEventListener("scroll", scroller);
        }
    }

    useEffect(() => {
        window.addEventListener("scroll", scroller);
        return () => window.removeEventListener("scroll", scroller);
    }, []);

    return (
        <>
            <div className="year-slider d-block w-75 gx-0 mx-auto">
                <div className="year-selector w-100 row">
                    {
                        education.map(ele => {
                            if(ele.id == 0)
                                return (<div key={"edu-bulb-" + ele.id} className="year-bulb col active" onClick={e => setActive(e)} title={ education[ele.id].time }></div>);
                            return (<div key={"education-" + ele.id} className="year-bulb col" title={ education[ele.id].time } onClick={e => setActive(e)}></div>);
                        })
                    }
                    <div className="year-bulb year-bulb-last"></div>
                </div>
            </div>
            <div id="education-box" className="container bg-light border education-box row mx-auto">
                <div id="edu-img" className="opacity-0 ol-12 col-lg-6 my-auto">
                    <img src={education[curEduPtr].src} className="p-0 p-sm-2 p-md-4 w-100" />
                </div>
                <div id="edu-info" className="opacity-0 col-12 col-lg-6 my-5">
                    <h3 className="text-center">{education[curEduPtr].heading}</h3>
                    <p className="text-center">({education[curEduPtr].time})</p>
                    <p className="edu-content text-secondary"><br/>{education[curEduPtr].content}</p>
                </div>
            </div>
        </>
    );
}

export default memo(Education);