import { memo, useEffect } from "react";

function AboutMe() {
    function scrollLoader() {
        const para = document.getElementById("about-me-p");
        const img = document.getElementById("about-me-img");

        if(img.getBoundingClientRect().top < window.scrollY + 200) {
            img.style.animation = "0.5s fadeInLeft ease-out forwards";
            img.classList.remove("opacity-0");
        }

        if(para.getBoundingClientRect().top < window.scrollY + 200) {
            para.style.animation = "0.5s fadeInUp ease-out forwards";
            para.classList.remove("opacity-0");
        }
    }

    useEffect(() => {
        window.addEventListener("scroll", scrollLoader, {passive: false});
        return () => { window.removeEventListener("scroll", scrollLoader); }
    }, []);

    return(
        <div id="About">
            <div className="container py-lg-3 my-5 about-me position-relative">
            {/* <h2 className="display-4 sec-heading">ABOUT ME</h2> */}
                <div id="about-me" className="container-fluid row g-0">
                    <img id="about-me-img" src={ process.env.PUBLIC_URL + "./images/misc/snapshot.png" } className="opacity-0 col-sm-3 mx-auto my-lg-0 my-2 col-6 p-3 p-md-4 p-lg-5"/>
                    <p id="about-me-p" className="opacity-0 col-lg-9 col-12 about-me-p my-auto">
                    Hello there! I am Shrey Naik. I am a second year undergraduate student pursuing B.Tech in Computer Science from Dharmsinh Desai University, Gujarat, India. I'm a front-end as well as a back-end developer always keen in learning and exploring more about the world of technology. I'm also interested in cyber security and ethical hacking. Apart from technology, I'm interested in football (soccer). I've collaborated with a colleague to work on an online cloth emporium project which was fully-functional using front as well as back end and also on a text editor like a mini MS-Word which was quite successfull too. I've always had a dream to work in the IT industry and I'm constantly putting my efforts to achieve my dreams.
                    </p>
                </div>
            </div>
        </div>
    );
}

export default memo(AboutMe);