import { memo, useState, useEffect } from "react";
import "../styles/contact.css";
import emailjs from "emailjs-com";

const contacts = [
    { id: 1, name: "Linkedin", URL: "https://www.linkedin.com/in/shrey-naik-52b395202", icon: "./images/contacts/linkedin.png" },
    { id: 2, name: "Codechef", URL: "https://www.codechef.com/users/naikshrey2308/", icon: "./images/contacts/codechef.jpg" },
    { id: 3, name: "GitHub", URL: "https://www.github.com/naikshrey2308", icon: "./images/contacts/github.png" },
    { id: 4, name: "Hackerrank", URL: "https://www.hackerrank.com/naikshrey2308?h_r=internal-search&hr_r=1", icon: "./images/contacts/hackerrank.svg" },
    { id: 5, name: "StackOverflow", URL: "https://stackoverflow.com/users/16478404/shrey-naik", icon: "./images/contacts/stackoverflow.png" },
    { id: 6, name: "Gmail", URL: "mailto:naikshrey2308@gmail.com", icon: "./images/contacts/gmail.png" }
];

function ContactIcons(props) {
    return (
        <>
            <div className="mx-auto d-block text-center">
                <img src="./images/svgs/location.svg" width="20px" />
                <p className="d-inline"> Surat, Gujarat, India</p>
            </div>
            <br/>
            <div className="container d-flex justify-content-center">
                {
                    props.info.map(ele => {
                        return (
                            <>
                                <a key={"contact-icon-" + ele.id} title={ele.name} href={ele.URL} target="_blank"><img src={ele.icon} width="30px" className="mx-2 my-4" /></a>
                            </>
                        );
                    })
                }
            </div>
            <br/>
        </>
    );
}

function ContactForm() {

    function sendEmail(e) {
        e.preventDefault(); 
        const submit = document.getElementById("submit-btn");
        submit.disabled = true;
        submit.innerHTML = "<span class='spinner-border spinner-border-sm'></span> Sending..."

        emailjs.sendForm('service_3t7g755', 'template_tiol0bo', e.target, 'fzlH3GYqKF90YNJP0')
        .then((result) => {
            submit.innerHTML = "&check; Sent!";
        }, (error) => {
            console.log(error.text);
        });
    }

    function check(id, ptrn, err, success) {
        const name = document.getElementById(id);
        const pattern = ptrn;
        const error = document.getElementById(`${id}-error`);
        if(!name.value.toString().match(pattern)) {
            name.classList.remove("valid");
            name.classList.add("invalid");
            error.classList.remove("text-success");
            error.classList.add("text-danger");
            error.innerHTML = err;
        } else {
            name.classList.remove("invalid");
            name.classList.add("valid");
            error.classList.remove("text-danger");
            error.classList.add("text-success");
            error.innerHTML = success;
        }
    }

    return (
        <>
            <div className="contact-form">
                <p className="px-3 px-md-5 text-secondary">Let's get in touch! Fill the below form and drop me a message. I'll reach out to you as soon as possible.</p>
                <form onSubmit={(e) => sendEmail(e)} className="form container px-3 px-md-5">
                    <input id="name" type="text" name="name" className="input-control form-control mt-3" placeholder="Name" pattern="^[a-zA-Z ]+$" onChange={() => { check("name", /^[a-z ]+$/i, "&cross; Name should contain alphabets and spaces only!", "&check; Perfect!") }} required/>
                    <p id="name-error" className="name-error text-danger"></p>

                    <input id="email" type="email" name="email" className="input-control form-control mt-3" placeholder="Email" pattern="^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w+)+$" onChange={() => { check("email", /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w+)+$/, "&cross; Email doesn't seem to be correct!", "&check; Email correct!") }} required/>
                    <p id="email-error" className="email-error text-danger"></p>

                    <textarea id="message" name="message" className="input-control form-control mt-3" placeholder="Message" rows={8} style={{resize: "none"}} required />
                    <p id="message-error" className="message-error text-danger"></p>

                    <button id="submit-btn" type="submit" className="btn submit-btn float-end">Send</button>
                </form>
                <div className="my-5">&nbsp;</div>
            </div>
        </>
    );
}

function Contact() {
    
    function scrollerFunc(event) {
        event.stopPropagation();
    }

    useEffect(() => {
        const scroller = (event) => scrollerFunc(event);
        const contact = document.getElementById("Contact");
        contact.addEventListener("scroll", scroller, {passive: false});
        return () => contact.removeEventListener("scroll", scroller);
    }, []);


    function close() {
        const contact = document.getElementById("Contact");
        contact.style.animation = "0.3s slideOutRight ease-in forwards";
        // skills.style.left = "-100%";
    }

    return (
        <>
            <div id="Contact" className="bg-light col-lg-4 col-sm-8 col-12">
                <div className="contact-container">
                    <div className="container text-center">
                        <h3 className="fw-bold my-5">Contact Me</h3>
                        <button className="btn btn-close top-0 end-0 m-3 position-absolute" onClick={close} />
                    </div>
                    <ContactIcons info={contacts} />
                    <ContactForm />
                </div>
            </div>
        </>
    );
}

export default memo(Contact);